@import "../css/reset";

@import "./variables.scss";
@import "./components/logo.scss";
@import "./components/menu.scss";
@import "./components/post.scss";
@import "./components/page.scss";

body {
    font-family: Roboto, sans-serif;
    color: $text_color;
    font-weight: 300;
}

header.Header, main, footer {
    background-color: $content_background;
    max-width: 800px;
    margin: 0 auto;
}

.Header {
    display: flex;
    align-items: center;
    padding-top: 80px;

    @media $phablet_mq {
        flex-direction: column;
        align-items: center;
    }
}

nav {
    flex: 1;
    text-align: right;
}

p + blockquote, p + p, blockquote + p {
    margin-top: 25px;
}

blockquote {
    padding-left: 25px;
    border-left: 2px solid $text_color;
}